/*------------- Style-01-----------*/
.circle-style.style-1 figure{
	border-radius: 50%;
	-webkit-border-radius: 50%;
}
.circle-style.style-1 .awsm-grid-holder{
	overflow: visible;
}
.circle-style.style-1 figure::before{
	content: "";
	background: rgba(0,0,0,0.75);
	position: absolute;
	left: 10px;
	top: 10px;
	right: 10px;
	bottom: 10px;
	opacity: 0;
	border-radius: 50%;
	-webkit-border-radius: 50%;
	-webkit-transform: scale(0.6);
	        transform: scale(0.6);
 transition: opacity 0.2s, -webkit-transform 0.4s;
 transition: transform 0.4s, opacity 0.2s;
 transition: transform 0.4s, opacity 0.2s, -webkit-transform 0.4s;
    z-index: 1;
}
.circle-style.style-1 figure img {
    transition: -webkit-transform 0.8s;
    transition: transform 0.8s;
    transition: transform 0.8s, -webkit-transform 0.8s;
    border-radius: 50%;
	-webkit-border-radius: 50%;
}
.circle-style.style-1 figcaption {
    bottom: 10px;
    left: 10px;
    right: 10px;
    position: absolute;
    border-radius: 50%;
	-webkit-border-radius: 50%;
    padding: 25px;
    color: #fff;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
	-webkit-transform: translateY(100%) translateY(-90px);
    transform: translateY(100%) translateY(-90px);
    transition: -webkit-transform 0.4s;
    transition: transform 0.4s;
    transition: transform 0.4s, -webkit-transform 0.4s;
    text-align: center;
    z-index: 2;
}
.circle-style.style-1 figcaption a{
	color: #fff;
}
.circle-style.style-1 figcaption p{
	font-size: 14px;
}
.circle-style.style-1 .awsm-contact-info{
	-webkit-transform: translateY(0%);
    transform: translateY(0%);
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
 transition: opacity 0.4s 0.2s, -webkit-transform 0.4s;
 transition: transform 0.4s, opacity 0.4s 0.2s;
 transition: transform 0.4s, opacity 0.4s 0.2s, -webkit-transform 0.4s;
    opacity: 0;
    visibility: hidden;
}
.circle-style.style-1 figure:hover::before,
.circle-style.style-1 figure.cs-hover::before{
	opacity: 1;
	-webkit-transform: scale(1);
	        transform: scale(1);
}
.circle-style.style-1 figure:hover img, .circle-style.style-1 figure.cs-hover img {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}
 .circle-style.style-1 figure:hover figcaption,
.circle-style.style-1 figure.cs-hover figcaption {
    -webkit-transform: translateY(0px) translateY(0px);
    transform: translateY(0px) translateY(0px);
}
 .circle-style.style-1 figure:hover .awsm-contact-info,
.circle-style.style-1 figure.cs-hover .awsm-contact-info {
	-webkit-transform: translateY(0px);
	transform: translateY(0px);
	opacity: 1;
    visibility: visible;
}
.circle-style.style-1 .awsm-grid-holder:hover figcaption {
    -webkit-transform: translateY(100%) translateY(-100%);
            transform: translateY(100%) translateY(-100%);
}
.circle-style.style-1 .awsm-grid-holder:hover figcaption .awsm-contact-info{
	visibility: visible;
}




